import React from "react";
import { BrowserRouter as Router,Route } from "react-router-dom";
import Login from './components/Login/login';
import Chat from './components/chat/chat';
const App =()=>(

<Router>
<Route basename={window.location.pathname || ''}  path="/" exact component ={Login}/>
<Route basename={window.location.pathname || ''} path="/chat" exact component={Chat}/>

</Router>



); 


export default App;


